body {
  padding-top: 101px; }

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px; }

.wrapper-button {
  width: 100%;
  text-align: center; }

.btn {
  display: block;
  width: 100%;
  max-width: 500px;
  margin: 10px auto;
  padding: 10px 35px;
  background-color: #100;
  color: #fff;
  font-size: 20px;
  border-width: 1px;
  border-style: solid;
  border-color: gray;
  border-radius: 6px; }
  .btn.btn-small {
    display: inline-block;
    max-width: 100px;
    font-size: 16px;
    padding: 10px 20px;
    margin: 10px 5px; }

@media screen and (max-width: 414px) {
  body {
    padding-top: 74px; } }



